import { Component, OnInit } from '@angular/core';
import { AlertService, gettext, ModalService, Status } from '@c8y/ngx-components';
import { AddTrustedCertificateComponent } from './add-trusted-certificate.component';
import { BsModalService } from 'ngx-bootstrap/modal';
import { ITrustedCertificate, TrustedCertificateService, IResultList } from '@c8y/client';
import { TranslateService } from '@ngx-translate/core';
import { tap, switchMap } from 'rxjs/operators';
import { pipe, BehaviorSubject, Observable, of } from 'rxjs';

@Component({
  selector: 'c8y-trusted-certificates',
  templateUrl: './trusted-certificates.component.html',
})
export class TrustedCertificatesComponent implements OnInit {
  reloading: BehaviorSubject<boolean> = new BehaviorSubject(false);
  reload: BehaviorSubject<void> = new BehaviorSubject(null);
  trustedCertificates: Observable<IResultList<ITrustedCertificate>> = this.reload.pipe(
    tap(() => this.reloading.next(true)),
    switchMap(() => this.getTrustedCertificates()),
    tap(() => this.reloading.next(false))
  );
  sortByExpirationDateAsc = pipe(
    tap((results: ITrustedCertificate[]) => {
      return results.sort((n1, n2) => n1.notAfter.localeCompare(n2.notAfter));
    })
  );

  constructor(
    private bsModal: BsModalService,
    private alertService: AlertService,
    private trustedCertificateService: TrustedCertificateService,
    private modalService: ModalService,
    private translateService: TranslateService
  ) {}

  async ngOnInit() {
    await this.loadTrustedCertificates();
  }

  async loadTrustedCertificates() {
    this.reload.next();
  }

  getTrustedCertificates() {
    const filter: object = {
      pageSize: 1000,
      withTotalPages: true,
    };
    return this.trustedCertificateService.list(filter);
  }

  async addTrustedCertificate() {
    const modal = this.bsModal.show(AddTrustedCertificateComponent, {
      class: 'modal-sm',
      ignoreBackdropClick: true,
    }).content as AddTrustedCertificateComponent;
    try {
      await modal.result;
      modal.close();
      this.reload.next();
    } catch (ex) {
      // do nothing
    }
  }

  async deleteTrustedCertificate(certificate, index) {
    const title = gettext('Delete trusted certificate');
    const confirmationText = gettext(
      'You are about to delete a trusted certificate "{{ certificateName }}".'
    );
    const finalQuestion = gettext('Do you want to proceed?');
    const certificateName = certificate.name;
    const body = [
      this.translateService.instant(confirmationText, {
        certificateName,
      }),
      this.translateService.instant(finalQuestion),
    ].join(' ');

    try {
      await this.modalService.confirm(title, body, Status.DANGER, { ok: gettext('Delete') });
      await this.trustedCertificateService.delete(certificate.fingerprint);
      this.alertService.success(gettext('Certificate deleted.'));
      this.reload.next();
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  async updateCertificate(
    trustedCertificate: ITrustedCertificate,
    updatingPart: Partial<ITrustedCertificate>
  ) {
    try {
      await this.trustedCertificateService.update({
        fingerprint: trustedCertificate.fingerprint,
        ...updatingPart,
      });
      this.alertService.success(gettext('Certificate saved.'));
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  highlightDependingOnExpirationStatus(item: ITrustedCertificate) {
    const warningOffset = 24 * 60 * 60 * 1000 * 90; // 90 days
    const todayTimestamp = new Date().getTime();
    const warningTimestamp = new Date().getTime() + warningOffset;
    const notAfterTimestamp = new Date(item.notAfter).getTime();
    const expired = notAfterTimestamp < todayTimestamp;
    const expiresInLessThan90Days = notAfterTimestamp < warningTimestamp;
    if (expired) {
      return 'text-danger';
    }
    if (expiresInLessThan90Days) {
      return 'text-warning';
    }
    return '';
  }
}
