import { Component } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { DroppedFile } from '@c8y/ngx-components';
import { ITrustedCertificate, TrustedCertificateService } from '@c8y/client';
import { AlertService, gettext } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-add-trusted-certificate',
  templateUrl: './add-trusted-certificate.component.html'
})
export class AddTrustedCertificateComponent {
  maxAllowedFiles = 1;
  AUTO_REGISTRATION_POPOVER = gettext('All devices with credentials signed by this certificate will be able to communicate with the platform without a prior registration.');
  trustedCertificate: Partial<ITrustedCertificate> = {
    status: 'DISABLED'
  };
  fileIsUploaded = false;

  result: Promise<any> = new Promise((resolve, reject) => {
    this._save = resolve;
    this._cancel = reject;
  });
  private _save;
  private _cancel;

  constructor(
    private modal: BsModalRef,
    private trustedCertificateService: TrustedCertificateService,
    private alertService: AlertService
  ) {}

  uploadFile(dropped: DroppedFile[]) {
    this.fileIsUploaded = false;
    if (dropped) {
      const reader = new FileReader();
      reader.onload = () => {
        this.fileIsUploaded = true;
        this.trustedCertificate.certInPemFormat = reader.result as string;
      };
      reader.readAsText(dropped[0].file);
    }
  }

  async save() {
    try {
      await this.trustedCertificateService.create(this.trustedCertificate);
      this.alertService.success(gettext('Certificate saved.'));
      this._save();
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  close() {
    this._cancel();
    this.modal.hide();
  }
}
